.name{
    display: flex;

}
.password{
    display: flex;

}
.form{
width: 440px;
height: 440px;
position: relative;
background: transparent;
border: 2px solid black;
display: flex;
align-items: center;
justify-content: center;
margin-left: auto;
margin-right: auto;
border-radius: 4px;
margin-top: 150px;
box-shadow: -2px 6px 15px 2px rgba(0, 0, 0, 0.57);

}
.inputBox{
    height: 80px;
    border-bottom: 2px solid ;
    margin: 30px 0;
    display: flex;
    align-items: center;
    justify-content: center;
}
.inputBox input {
    border: transparent;
    font-size: 16px;
        line-height: 20px;
        color: rgba(255, 255, 255, 0.6);
        height: 33px;
        padding-left: 16px;
        border: 1px solid rgba(255, 255, 255, 0.3);
        -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.15));
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.15));
        border-radius: 4px;
        background: #2f303a;
}
.icon{
    margin-right: 10px;
}